  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin:30px 0px 30px 0px;
}

.widecard:hover
{
  color:#1b1d20;
  background-color: white;
  box-shadow:0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.projectlink
{
  -webkit-align-self: center;
          align-self: center;
  padding-right: 20px;
  margin-left: auto;
  font-size:40px;
}

.projectlink i:not(:hover)
{
  color:black;
  transition-duration: 0.3s;
}

.projectlink i:hover
{
  color:gray;
  transition-duration: 0.3s;
}

.emaillink
{
  color:black;
}
.nav-item:not(:hover) {
    transition-duration: 0.3s;
}

.nav-item:hover {
    transition-duration: 0.3s;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.social {
    position: relative;
    bottom: 1px;
    top: auto;
}
.game-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.board {
    display: grid;
    border-style: solid;
    border-width: 10px;
    border-radius: 10px;
    background-color: gray;
    width: 250px;
    height: 250px;
    margin: 20px 0 20px 0;
    grid-gap: 10px;
    grid-template: repeat(3, minmax(0, 1fr)) / repeat(3, minmax(0, 1fr));
    /* grid-template:
        '. . .'
        '. . .'
        '. . .'; */
    overflow: hidden;
}

.start-quit-button {
    color: black;
    background-color: white;
    height: 50px;
    width: 200px;
    border-style: solid;
    border-radius: 10px;
    border-width: 5px;
    font-size: 15px;
    text-align: center;
}

.start-quit-button:not(:hover) {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-color: black;
    transition-duration: 0.3s;
}

.start-quit-button:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    border-color: gray;
    transition-duration: 0.3s;
}

.start-button {
    margin: 20px 0 10px 0;
}

.game-space {
    max-width: 77px;
    max-height: 77px;
    font-size: xx-large;
    font-weight: bold;
    overflow: hidden;
}

.game-space:not(:hover) {
    background-color: white;
    border-style: none;
    transition-duration: 0.3s;
}

.game-space:hover {
    border-style: none;
    background-color: rgba(132, 124, 252, 1);
    transition-duration: 0.3s;
}

.game-space:active {
    background-color: rgb(48, 119, 225);
    border-style: none;
    transition-duration: 0ms;
}

.spinner {
    margin-left: 10px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
h2 {
    color: black;
    text-decoration: none;
}

.deal {
    text-align: center;
    margin-top: 50px;
}

