    -webkit-transform: scale(1);
            transform: scale(1);
    transition-duration: 0.3s;
}

.skill-box:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    box-shadow: -5px 5px 5px 5px rgb(223, 223, 223);
    transition-duration: 0.3s;
}
